<template>
    <div>
        <div class="container">
            <h2 class="text-center mt-4"></h2>

            <div class="text-right">
                <!-- <button class="btn btn-outline-dark" @click="openCouponModal(true)"> coupon</button> -->
                <button class="btn btn-outline-primary" @click="openCouponModal(true)"> coupon</button>
            </div>

            <p class="h5 text-center mt-5" v-if="coupons.length == 0">~</p>

            <table class="table table-bordered table-striped table-responsive-lg mt-4">
                <thead class="thead-dark">
                    <tr>
                        <!-- title -->
                        <th class="text-center align-middle">coupon </th>

                        <!-- code -->
                        <th class="text-center align-middle">coupon </th>

                        <!-- percent -->
                        <th class="text-center align-middle"></th>

                        <!-- is_enabled -->
                        <th class="text-center align-middle"></th>

                        <!-- due_date -->
                        <th class="text-center align-middle"></th>

                        <th width="100px" class="text-center align-middle"></th>
                        <th width="100px" class="text-center align-middle"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in coupons" :key="item.id">
                        <!-- <td>{{item}}</td> -->
                        <!-- coupon  -->
                        <td class="text-center align-middle">{{ item.title }}</td>
                        <!-- coupon  -->
                        <td class="align-middle">{{ item.code }}</td>
                        <!--  -->
                        <td class="text-center align-middle">{{ item.percent|couponTransfer }}</td>
                        <!--  -->
                        <td class="text-center align-middle">
                            <span v-if="item.is_enabled" class="text-success"></span>
                            <span v-else class="text-secondary"></span>
                        </td>
                        <!--  -->
                        <td class="text-center align-middle">{{ item.due_date|couponFormatTime }}</td>
                        <td class="text-center align-middle">
                            <button class="btn btn-outline-info btn-sm"
                                @click="openCouponModal(false, item)"></button>
                            <!-- <button class="btn btn-outline-primary btn-sm"
                                @click="openCouponModal(false, item)"></button> -->
                        </td>
                        <td class="text-center align-middle">
                            <button class="btn btn-outline-danger btn-sm"
                                @click="openDeleteModal(item.id, item.title)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--  -->
        <nav aria-label="Page navigation example" class="mt-5 mb-4">
            <ul class="pagination justify-content-center">
                <li class="page-item" :class="{'disabled' : !pagination.has_pre }">
                    <a class="page-link" href="#" aria-label="Previous"
                        @click.prevent="getProducts(pagination.current_page - 1)">
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                </li>
                <li class="page-item" :class="{ 'active' : pagination.current_page === page }"
                    v-for="page in pagination.total_pages" :key="page">
                    <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
                </li>
                <li class="page-item" :class="{'disabled' : !pagination.has_next }">
                    <a class="page-link" href="#" aria-label="Next"
                        @click.prevent="getProducts(pagination.current_page + 1)">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                </li>
            </ul>
        </nav>


        <!--  coupon Modal -->
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span v-if="modalTitle"></span>
                            <span v-else></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <!-- <div class="col-12"> -->
                        <div>
                            <div class="form-group">
                                <label for="title">coupon </label>
                                <input type="text" class="form-control" id="title" v-model="tempCoupon.title">
                            </div>
                            <div class="form-group">
                                <label for="code">coupon </label>
                                <input type="text" class="form-control" id="code" v-model="tempCoupon.code">
                            </div>
                            <div class="form-group">
                                <label for="percent"><br>(9"90"8"80"~)</label>
                                <input type="number" max="100" min="0" class="form-control" id="percent"
                                    v-model="tempCoupon.percent">
                            </div>
                            <div class="form-group">
                                <label for="due_date"></label>
                                <input type="date" class="form-control" id="due_date" v-model="tempCoupon.due_date">
                            </div>
                            <div class="form-group">
                                <div class="form-check">
                                    <input class="form-check-input" type="checkbox" id="is_enabled"
                                        v-model="tempCoupon.is_enabled" v-bind:true-value="1" v-bind:false-value="0">
                                    <label class="form-check-label" for="is_enabled"></label>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer border-top-0">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateCoupon" v-if="isNew"></button>
                        <button type="button" class="btn btn-primary" @click="updateCoupon" v-else></button>
                        <!-- <button type="button" class="btn btn-success" @click="testData"></button> -->
                    </div>
                </div>
            </div>
        </div>

        <!--  Modal  -->
        <div class="modal fade" id="couponDeleteModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel"> coupon </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <span class="h6">{{ this.deleteCouponTitle }}</span>
                    </div>
                    <div class="modal-footer">
                        <div class="container-fluid">
                            <div class="row">
                                <button type="button" class="btn btn-secondary col mr-1"
                                    data-dismiss="modal"></button>
                                <!-- <div class="col-2 d-sm-none"></div> -->
                                <button type="button" class="btn btn-danger col ml-1" @click="deleteCoupon">
                                    coupon</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
    import $ from "jquery";

    export default {
        data: function () {
            return {
                coupons: {},  //  coupon 
                pagination: {}, // 
                tempCoupon: {}, //  Modal 
                isNew: false,
                modalTitle: true, // true:""false:""
                deleteCouponID: "", //  coupon  ID
                deleteCouponTitle: "", //  coupon  title
            };
        },
        methods: {

            //  coupon 
            getCouponList(page = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
                const vm = this;
                // vm.isLoading = true;

                //     
                this.$http.get(api).then((response) => {
                    // console.log(response);  // 
                    vm.coupons = response.data.coupons;
                    vm.pagination = response.data.pagination;  // 
                    // vm.isLoading = false;
                });
            },

            openCouponModal(isNew, item) {
                if (isNew) {
                    //  coupon
                    this.tempCoupon = {};
                    this.isNew = true;
                    this.modalTitle = true;
                }
                else {
                    //  coupon
                    this.tempCoupon = Object.assign({}, item);
                    this.isNew = false;
                    this.modalTitle = false;
                }

                $("#couponModal").modal("show");
            },

            testData() {
                // this.tempCoupon.due_date = new Date(this.tempCoupon.due_date_rawdata).getTime();
                console.log(this.tempCoupon);
                console.log(this.tempCoupon.num);
            },

            updateCoupon() {
                // this.tempCoupon.due_date = new Date(this.tempCoupon.due_date_rawdata).getTime();
                //  unixTimeStamp
                this.tempCoupon.due_date = new Date(this.tempCoupon.due_date).getTime();
                // console.log(this.tempCoupon);

                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
                let httpMethod = "post";
                const vm = this;

                if (!vm.isNew) {
                    //  coupon
                    api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
                    httpMethod = "put";
                }

                this.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
                    // this.$http.post(api, { data: vm.tempCoupon }).then((response) => { 
                    // 

                    // console.log("updateCouponResponse", response);  // 
                    if (response.data.success) {
                        if (vm.isNew) {
                            console.log(" coupon!");
                        }
                        else {
                            console.log(" coupon!");
                        }
                        $("#couponModal").modal("hide"); //  orModal 
                        vm.getCouponList(); // ()
                    }
                    else {
                        if (vm.isNew) {
                            alert(" coupon");
                        }
                        else {
                            alert(" coupon ");
                        }
                        $("#couponModal").modal("hide"); //  orModal 
                        // vm.getCouponList(); // 
                    }
                });
            },

            //  coupon?
            openDeleteModal(id, title) {
                this.deleteCouponID = id;        //  id 
                this.deleteCouponTitle = title;  //  title 
                // console.log("deleteCouponID", this.deleteCouponID); // 
                // console.log("deleteCouponTitle", this.deleteCouponTitle); // 
                $("#couponDeleteModal").modal("show");
            },

            //  coupon
            deleteCoupon() {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.deleteCouponID}`;
                const vm = this;

                this.$http.delete(api).then((response) => {
                    console.log("deleteCoupon Response", response); // 

                    if (response.data.success) {
                        vm.getCouponList(); // ()
                        vm.deleteCouponID = "";    //  coupon id
                        vm.deleteCouponTitle = ""; //  coupon title
                        alert(" coupon"); // 
                        // $("#couponDeleteModal").modal("hide"); //  Modal 
                    }
                    else {
                        vm.deleteCouponID = "";    //  coupon id
                        vm.deleteCouponTitle = ""; //  coupon title
                        alert(" coupon");
                        // $("#couponDeleteModal").modal("hide"); //  Modal 
                    }
                    $("#couponDeleteModal").modal("hide"); //  Modal 
                });
            },



        },
        created() {
            this.getCouponList();
        },
    }
</script>


