<template>
    <div class="container-fluid p-0">

        <headerComponent />

        <div class="container orderData">
            <h1 class="h3 text-center my-4">  </h1>

            <div class="row my-3">
                <div class="col-4">
                    <h3> id</h3>
                </div>
                <div class="col-8">
                    <p style="word-break: break-all;">{{orderData.id}}</p>
                </div>
            </div>

            <div class="row my-3">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8">
                    <p class="text-right">{{orderData.create_at|formatTime}}</p>
                </div>
            </div>

            <div class="row my-3 orderProduct">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8">
                    <div v-for="item in orderData.products" class="my-4">
                        <h4>{{item.product.title}}</h4>
                        <img :src="item.product.imageUrl" alt="">
                        <h4 class="mt-2">{{item.qty}}</h4>
                        <h4>{{item.final_total|currency}}</h4>
                    </div>
                </div>
            </div>

            <div class="row my-3">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8">
                    <p>{{orderData.total|currency}}</p>
                </div>
            </div>

            <div class="row my-3 is_paid">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8 d-flex justify-content-center align-items-center">
                    <p v-if="orderData.is_paid" class="m-0 text-success"></p>
                    <p v-if="!orderData.is_paid" class="m-0 text-danger"></p>
                    <button v-if="!orderData.is_paid" class="btn btn-sm btn-primary ml-3"
                        @click.prevent="payOrder(orderData.id)"></button>
                </div>
            </div>

            <div class="row my-3 userData">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8">
                    <div>
                        <h4>email&nbsp;&nbsp;</h4>
                        <p>{{orderData.user.email}}</p>
                    </div>
                    <div>
                        <h4></h4>
                        <p>{{orderData.user.name}}</p>
                    </div>
                    <div>
                        <h4></h4>
                        <p>{{orderData.user.tel}}</p>
                    </div>
                    <div>
                        <h4>&emsp;</h4>
                        <p>{{orderData.user.address}}</p>
                    </div>
                </div>
            </div>

            <div class="row my-3 userMessage">
                <div class="col-4">
                    <h3></h3>
                </div>
                <div class="col-8">
                    <p>{{orderData.message}}</p>
                </div>
            </div>
        </div>

        <footerComponent />

    </div>
</template>

<style scoped>
    .orderData {
        max-width: 510px;
    }

    .orderData .row {
        /* .row  display: flex;  */
        justify-content: space-between;
        align-items: center;
    }

    .orderData h3 {
        /*  */
        font-size: 1rem;
        margin: 0;
    }

    .orderData h4 {
        /*  */
        font-size: 0.875rem;
        line-height: 0.875rem;
    }

    .orderData p {
        /*  */
        margin: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        /*  */
    }

    .orderProduct .col-8>div {
        /*  */
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .orderProduct img {
        /*  */
        width: 100px;
    }

    /*  */
    .userData .col-8>div {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .userData .col-8 h4 {
        width: 90px;
        margin: 0;
        white-space: nowrap;
        /*  */
    }

    .userData .col-8 p {
        word-break: break-all;
    }

    /*  */
    .userMessage p {
        word-break: break-all;
        /*  flex-end */
        justify-content: flex-start;
    }
</style>

<script>
    import headerComponent from "../headerComponent";
    import footerComponent from "../footerComponent";


    export default {
        name: "singleOrder",

        components: {
            headerComponent,
            footerComponent,
        },

        data() {
            return {
                orderId: "",
                orderData: {},
            }
        },

        methods: {

            //  id  data return 
            judgeOrderByRouterParam() {
                this.orderId = this.$route.query.orderId;
                console.log("this.$route.query.orderId", this.$route.query.orderId);
                console.log("this.orderId", this.orderId);
            },

            // 
            getOrderData() {
                const vm = this;
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
                this.$http.get(api).then((response) => {
                    console.log("orderResponse", response); // 
                    if (response.data.success) {
                        vm.orderData = response.data.order;
                        console.log("vm.orderData", vm.orderData);
                    }
                });
            },

            // 
            payOrder() {
                const vm = this;
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;

                this.$http.post(api).then((response) => {
                    // console.log("response", response); // 
                    if (response.data.success) {
                        alert("");
                        // console.log("");
                        vm.getOrderData();
                        vm.scrollTop();
                    }
                });
            },

            // 
            scrollTop() {
                document.body.scrollTop = document.documentElement.scrollTop = 0;
            },
        },

        created() {
            this.scrollTop();
            this.judgeOrderByRouterParam(); //  id  data return 
            this.getOrderData();
        },
    }

</script>


