<template>
    <div class="conainer-fluid p-0">

        <form class="form-signin" @submit.prevent="signin">
            <h1 class="h3 mb-3 font-weight-normal"></h1>
            <label for="inputEmail" class="sr-only"></label>
            <input type="email" id="inputEmail" class="form-control" placeholder="" v-model="user.username"
                required>
            <label for="inputPassword" class="sr-only"></label>
            <input type="password" id="inputPassword" class="form-control" placeholder="" v-model="user.password"
                required>
            <div class="checkbox mb-3">
                <label>
                    <input type="checkbox" value="remember-me"> 
                </label>
            </div>
            <button class="btn btn-primary btn-block" type="submit"></button>
        </form>

    </div>
</template>


<style scoped>
    .form-signin {
        height: calc(100vh - 60px);
        text-align: center;
        max-width: 330px;
        padding: 95px 0;
        margin: 0 auto;
    }

    @media (min-width: 992px) {
        .form-signin {
            height: calc(100vh - 78px);
        }
    }

    .form-signin .checkbox {
        font-weight: 400;
    }

    .form-signin .form-control {
        position: relative;
        box-sizing: border-box;
        height: auto;
        padding: 10px;
        font-size: 16px;
    }

    .form-signin .form-control:focus {
        z-index: 2;
    }

    .form-signin input[type="email"] {
        margin-bottom: -1px;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }

    .form-signin input[type="password"] {
        margin-bottom: 10px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
</style>


<script>
    export default {

        name: "adminLogin", // 

        data() {
            return {
                user: {
                    username: "",
                    password: "",
                },
            }
        },

        methods: {

            // 
            signin() {
                // const api = `${process.env.APIPATH}/signin`; 
                const api = `${process.env.APIPATH}/admin/signin`; // 
                const vm = this;

                this.$http.post(api, vm.user).then((response) => {
                    // console.log("response", response);  // 

                    if (response.data.success) { // 
                        const token = response.data.token;
                        const expired = response.data.expired;
                        // console.log(token, expired); // 

                        //  cookie 
                        document.cookie = `hexToken=${token};expires=${new Date(expired)};`; // cookie

                        alert("");
                        vm.$router.push("/admin_Dashboard"); //  // 
                    }
                    else {
                        alert("QQ");
                        location.reload(); // 
                    }
                });
            },

        },

        created() {
            const myCookie = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, "$1");
            // console.log("myCookie", myCookie);
            this.$http.defaults.headers.common.Authorization = myCookie;
        },

    };
</script>


