// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

//  axios 
import axios from 'axios'
import VueAxios from 'vue-axios'

// 
import router from './router'

//  Bootstrap
import 'bootstrap'

//  vee-validate 
import { ValidationObserver, ValidationProvider, extend, localize, configure } from 'vee-validate';
import TW from 'vee-validate/dist/locale/zh_TW.json'
import * as rules from 'vee-validate/dist/rules'

//  vue-slider 
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'

//   ( ) filter
import currencyFilter from './filters/currency'
import formatTime from './filters/formatTime'
import couponFormatTime from './filters/couponFormatTime'
import couponTransfer from './filters/couponTransfer'

import eventBus from '@/eventBus.js'

// 
axios.defaults.withCredentials = true

//  filter  
Vue.use(VueAxios, axios) //  VueAxios 
Vue.filter('currency', currencyFilter) //   ( ) filter
Vue.filter('formatTime', formatTime) //  unxiTimeStamp   filter
Vue.filter('couponFormatTime', couponFormatTime) //  unxiTimeStamp   filtercoupon 
Vue.filter('couponTransfer', couponTransfer) //  % filter

//  vue-slider  
Vue.component('VueSlider', VueSlider)


//  vee-validate 
Object.keys(rules).forEach((rule) => {
	extend(rule, rules[rule]);
});

localize('zh_TW', TW);

Vue.component('ValidationObserver', ValidationObserver)
Vue.component('ValidationProvider', ValidationProvider)

configure({
	classes: {
		valid: 'is-valid',
		invalid: 'is-invalid'
	}
});


// 
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
	el: '#app',
	components: { App },
	template: '<App/>',
	router,  // 
});

router.beforeEach((to, from, next) => {
	// console.log("to", to);
	// console.log("from", from);
	// console.log("next", next);
	if (to.meta.requiresAuth) { // ()
		const api = `${process.env.APIPATH}/api/user/check`;
		axios.post(api).then((response) => {
			// console.log(response);
			if (response.data.success) { // 
				next();
			}
			else {
				alert("~")
				next({
					path: "/login",
				});
			}
		});
	}
	else if (to.meta.requiresAdminAuth) { // ()
		const api = `${process.env.APIPATH}/api/user/check`;
		axios.post(api).then((response) => {
			// console.log(response);
			if (response.data.success) { // 
				next();
			}
			else {
				alert("~")
				next({
					path: "/admin_Dashboard/adminLogin",
				});
			}
		});
	}
	else {
		next();
	}
});


