// 
import Vue from 'vue';
import VueRouter from 'vue-router';

// 
// import HelloWorld from '@/components/HelloWorld';
// 
import home from '@/components/pages/home'; // 
import login from '@/components/pages/login'; // 
import logout from '@/components/pages/logout'; // 
import shop from '@/components/pages/shop'; // 
import singleProduct from '@/components/pages/singleProduct'; // 
import cart from '@/components/pages/cart'; // 
import order from '@/components/pages/order'; // 
import singleOrder from '@/components/pages/singleOrder'; // 
import contactUs from '@/components/pages/contactUs'; // 

// 
import admin_Dashboard from '@/components/pages/admin_Dashboard';
// import adminSimulatedOrder from '@/components/pages/adminSimulatedOrder';
import adminHome from '@/components/pages/adminHome'; // 
import adminLogin from '@/components/pages/adminLogin'; // 
import adminProducts from '@/components/pages/adminProducts'; // 
import adminCoupon from '@/components/pages/adminCoupon'; //  coupon
import adminOrder from '@/components/pages/adminOrder'; // 
// import test from '@/components/pages/test';


// 
Vue.use(VueRouter);

//  entry ( main.js)

//  
export default new VueRouter({

    routes: [

        {
            // 
            path: "*",
            redirect: "/", // 
        },

        {
            // 
            name: "home",
            path: "/",
            component: home,
        },

        {
            //  - 
            name: "shop",
            path: "/shop",
            component: shop,
        },
        {
            //  - 
            name: "singleProduct",
            path: "/singleProduct",
            component: singleProduct,
        },

        {
            // 
            name: "login",
            path: "/login",
            component: login,
            //  login.vue 
        },

        {
            // 
            name: "logout",
            path: "/logout",
            component: logout,
        },

        {
            // 
            name: "order",
            path: "/order",
            component: order,
            meta: { requiresAuth: true }, // 
            // meta: { abc: true }, // meta 
        },

        {
            // 
            name: "cart",
            path: "/cart",
            component: cart,
        },

        {
            // ()
            name: "singleOrder",
            path: "/singleOrder",
            component: singleOrder,
        },

        {
            // 
            name: "contactUs",
            path: "/contactUs",
            component: contactUs,
        },

        {
            // name: "admin_Dashboard", //  name 
            // vue-router.esm.js?fe87:16 [vue-router] Named Route 'admin_Dashboard' has a default child route. When navigating to this named route (:to="{name: 'admin_Dashboard'"), the default child route will not be rendered. Remove the name from this route and use the name of the default child route for named links instead.
            path: "/admin_Dashboard",
            component: admin_Dashboard,
            children: [
                {
                    name: "adminHome",
                    path: "", // 
                    component: adminHome,
                },
                {
                    name: "adminLogin",
                    path: "adminLogin",
                    component: adminLogin,
                },
                {
                    name: "adminProducts",
                    path: "adminProducts",
                    component: adminProducts,
                    meta: { requiresAdminAuth: true }, // 
                },
                {
                    name: "adminCoupon",
                    path: "adminCoupon",
                    component: adminCoupon,
                    meta: { requiresAdminAuth: true }, // 
                },
                {
                    name: "adminOrder",
                    path: "adminOrder",
                    component: adminOrder,
                    meta: { requiresAdminAuth: true }, // 
                },
            ]
        },
    ]
});


