<template>
    <div>

        <headerComponent />

        <h2 class="text-center mt-4"></h2>

        <div class="container">

            <p class="h5 text-center my-5" v-if="rawData.length == 0">~</p>

            <!--  -->
            <table class="table table-bordered table-striped table-responsive-xl mt-4 mw-100"
                v-if="rawData.length != 0">
                <thead class="thead-dark">
                    <tr>
                        <!-- id -->
                        <th width="80px" class="text-center align-middle"> id</th>
                        <!-- create_at -->
                        <th width="100px" class="text-center align-middle"></th>
                        <!-- is_paid -->
                        <th width="100px" class="text-center align-middle"></th>
                        <!-- products -->
                        <th width="250px" class="text-center align-middle"></th>
                        <!-- total -->
                        <th width="120px" class="text-center align-middle"></th>
                        <!-- message -->
                        <!-- <th width="120px" class="text-center align-middle"></th> -->
                        <th width="20%" class="text-center align-middle"></th>
                        <!-- user: Object -->
                        <!-- <th class="w-auto text-center align-middle"></th> -->
                        <th width="120px" class="text-center align-middle"></th>
                    </tr>
                </thead>

                <tbody>
                    <tr v-for="(order,index) in rawData" :key="index">
                        <!--  id -->
                        <td class="align-middle" style="word-break: break-all;">{{order.id}}</td>
                        <!--  -->
                        <td class="text-center align-middle" style="word-break: keep-all;">
                            {{order.create_at|formatTime}}
                        </td>
                        <!--  -->
                        <td class="text-center align-middle">
                            <span v-if="order.is_paid" class="text-success"></span>
                            <span v-if="!order.is_paid" class="text-danger"></span>
                            <button v-if="!order.is_paid" class="btn btn-sm btn-primary mt-1"
                                @click="payOrder(order.id)"></button>
                        </td>
                        <!--  -->
                        <td class="align-middle">
                            <div v-for="item in order.products"
                                class="d-flex flex-column justify-content-center align-items-center py-2">
                                <div class="d-flex justify-content-center align-items-center">
                                    <!--  -->
                                    <div>
                                        <img :src="item.product.imageUrl" alt="" style="width: 100px;">
                                        <br>
                                        <div class="text-center mt-1">{{item.product.title}}</div>
                                    </div>
                                    <!--  -->
                                    <div class="ml-3">
                                         {{item.qty}}
                                    </div>
                                </div>
                            </div>
                        </td>
                        <!--  -->
                        <td class="text-center align-middle">{{order.total|currency}}</td>
                        <!--  -->
                        <td style="max-width: 20%;  word-break: break-all;">{{order.message}}</td>
                        <!-- <td style="max-width: 20%;  word-wrap:break-word;" class="align-middle">{{order.message}}</td> -->
                        <!--  -->
                        <td class="align-middle">
                             email
                            <br>
                            {{order.user.email}}
                            <br>
                            <br>
                            
                            <br>
                            {{order.user.address}}
                            <br>
                            <br>
                            
                            <br>
                            {{order.user.name}}
                            <br>
                            <br>
                            
                            <br>
                            {{order.user.tel}}
                        </td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <nav aria-label="Page navigation example" class="mt-5 mb-4" v-if="rawData.length != 0">
                <ul class="pagination justify-content-center">
                    <li class="page-item" :class="{'disabled' : !pagination.has_pre }">
                        <a class="page-link" href="#" aria-label="Previous">
                            <span aria-hidden="true">&laquo;</span>
                            <span class="sr-only">Previous</span>
                            <!-- sr-only  screen reader only class  accessbility -->
                        </a>
                    </li>
                    <li class="page-item" :class="{ 'active' : pagination.current_page === page }"
                        v-for="page in pagination.total_pages" :key="page">
                        <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
                    </li>
                    <li class="page-item" :class="{'disabled' : !pagination.has_next }">
                        <a class="page-link" href="#" aria-label="Next">
                            <span aria-hidden="true">&raquo;</span>
                            <span class="sr-only">Next</span>
                        </a>
                    </li>
                </ul>
            </nav>

        </div>

        <footerComponent />

    </div>
</template>


<script>
    import $ from "jquery";

    import headerComponent from "../headerComponent";
    import footerComponent from "../footerComponent";

    export default {
        name: "order",

        components: {
            headerComponent,
            footerComponent
        },

        data() {
            return {
                rawData: [],
                pagination: {},
                tempOrder: {
                    products: {},
                    user: "",
                },
            }
        },

        methods: {

            getRawData(page = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/orders?page=${page}`;
                const vm = this;

                //     
                this.$http.get(api).then((response) => {
                    // console.log("response", response); // 
                    vm.rawData = response.data.orders;
                    vm.pagination = response.data.pagination;
                });
            },

            // 
            payOrder(orderId) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${orderId}`;
                const vm = this;

                this.$http.post(api).then((response) => {
                    // console.log("response", response); // 
                    if (response.success) {
                        alert("");
                        vm.getRawData();
                    }
                });
            },

        },

        created() {
            this.getRawData();
        },
    }
</script>


