<template>
    <div>
        <div class="container">
            <h2 class="text-center mt-4"></h2>

            <div class="text-right">
                <button class="btn btn-outline-primary" @click="openModal(true)"></button>
            </div>

            <p class="h5 text-center mt-5" v-if="products.length == 0">~</p>
            <!-- <p class="h5 text-center mt-5">~</p> -->

            <!--  -->
            <table class="table table-bordered table-striped table-responsive-lg mt-4" v-if="products.length != 0">
                <thead class="thead-dark">
                    <tr>
                        <th width="120px" class="text-center align-middle"></th>
                        <th width="80px" class="text-center align-middle"></th>
                        <th class="text-center align-middle" style="min-width: 90px;"></th>
                        <th width="80px" class="text-center align-middle"></th>
                        <th width="80px" class="text-center align-middle"></th>
                        <th width="100px" class="text-center align-middle"></th>
                        <th width="80px" class="text-center align-middle"></th>
                        <th width="80px" class="text-center align-middle"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in products" :key="item.id">
                        <td class="text-center py-3">
                            <img v-bind:src="item.imageUrl" width="80px" alt="">
                        </td>
                        <td class="text-center align-middle">{{ item.category }}</td>
                        <td class="align-middle">{{ item.title }}</td>
                        <td class="text-right align-middle">{{ item.origin_price | currency }}</td>
                        <td class="text-right align-middle">{{ item.price | currency }}</td>
                        <td class="text-center align-middle">
                            <span v-if="item.is_enabled" class="text-success"></span>
                            <span v-else class="text-secondary"></span>
                        </td>
                        <td class="text-center align-middle">
                            <button class="btn btn-outline-info btn-sm" @click="openModal(false, item)"></button>
                        </td>
                        <td class="text-center align-middle">
                            <button class="btn btn-outline-danger btn-sm"
                                @click="openDeleteModal(item.id, item.title)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <nav aria-label="Page navigation example" class="mt-5 mb-4">
                <ul class="pagination justify-content-center">
                    <li class="page-item" :class="{'disabled' : !pagination.has_pre }">
                        <a class="page-link" href="#" aria-label="Previous"
                            @click.prevent="getProducts(pagination.current_page - 1)">
                            <span aria-hidden="true">&laquo;</span>
                        </a>
                    </li>
                    <li class="page-item" :class="{ 'active' : pagination.current_page === page }"
                        v-for="page in pagination.total_pages" :key="page">
                        <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
                    </li>
                    <li class="page-item" :class="{'disabled' : !pagination.has_next }">
                        <a class="page-link" href="#" aria-label="Next"
                            @click.prevent="getProducts(pagination.current_page + 1)">
                            <span aria-hidden="true">&raquo;</span>
                        </a>
                    </li>
                </ul>
            </nav>
        </div>

        <!--  Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <!-- <span></span> -->
                            <span v-if="modalTitle"></span>
                            <span v-else></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            <!-- X  -->
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="imageUrl"></label>
                                    <input type="text" class="form-control" id="imageUrl" v-model="tempProduct.imageUrl"
                                        placeholder="">
                                </div>
                                <div class="form-group">
                                    <label for="customFile"> 
                                        <!-- <i class="fas fa-spinner fa-spin"></i> -->
                                    </label>
                                    <input type="file" id="customFile" class="form-control" ref="files">
                                </div>

                                <!--      -->
                                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                                    v-bind:src="tempProduct.imageUrl" class="img-fluid" alt="">
                                <!--  -->
                                <!--  v-model  v-bind:src -->
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="title" v-model="tempProduct.title"
                                        placeholder="">
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category"></label>
                                        <input type="text" class="form-control" id="category"
                                            v-model="tempProduct.category" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit"
                                            placeholder="">
                                    </div>
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price"></label>
                                        <input type="number" class="form-control" id="origin_price"
                                            v-model="tempProduct.origin_price" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="number" class="form-control" id="price" v-model="tempProduct.price"
                                            placeholder="">
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label for="description"></label>
                                    <textarea type="text" class="form-control" id="description"
                                        v-model="tempProduct.description" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <label for="content"></label>
                                    <textarea type="text" class="form-control" id="content"
                                        v-model="tempProduct.content" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" id="is_enabled"
                                            v-model="tempProduct.is_enabled" v-bind:true-value="1"
                                            v-bind:false-value="0">
                                        <label class="form-check-label" for="is_enabled">
                                            
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer border-top-0">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                    </div>
                </div>
            </div>
        </div>

        <!--  Modal  -->
        <div class="modal fade" id="productDeleteModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel"></h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <span class="h5">{{ this.deleteProductTitle }}</span>
                    </div>
                    <div class="modal-footer">
                        <div class="container-fluid">
                            <div class="row">
                                <button type="button" class="btn btn-secondary col mr-1"
                                    data-dismiss="modal"></button>
                                <button type="button" class="btn btn-danger col ml-1"
                                    @click="deleteProduct"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import $ from "jquery";

    export default {
        data: function () {
            return {
                pagination: {},
                products: [],     // 
                tempProduct: {},  //  Modal 
                modalTitle: true, // true:""false:""
                isNew: false,     // 
                deleteProductID: "", //  ID
                deleteProductTitle: "", //  title ()
                isLoading: false,
                status: {
                    fileUploading: false,
                },
            };
        },

        methods: {
            getProducts(page = 1) {
                // page = 1  1

                // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
                const vm = this;
                // vm.isLoading = true;

                //     
                this.$http.get(api).then((response) => {
                    // console.log(response.data);
                    vm.products = response.data.products;
                    vm.pagination = response.data.pagination;
                    vm.isLoading = false;
                });
            },

            openModal(isNew, item) {
                if (isNew) {
                    // 
                    // 
                    this.tempProduct = {};
                    this.isNew = true;
                    this.modalTitle = true;
                }
                else {
                    // 
                    // 
                    this.tempProduct = Object.assign({}, item);
                    this.isNew = false;
                    this.modalTitle = false;
                }
                $("#productModal").modal("show"); // BS4 // https://getbootstrap.com/docs/4.1/components/modal/#modalshow
            },

            //  Modal 
            updateProduct() {
                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
                let httpMethod = "post";
                const vm = this;

                if (!vm.isNew) {
                    // 
                    api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                    httpMethod = "put";
                }

                this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
                    // this.$http.post(api, { data: vm.tempProduct }).then((response) => { 
                    // 

                    // console.log(response.data);
                    if (response.data.success) {
                        $("#productModal").modal("hide"); //  orModal 
                        vm.getProducts(); // ()
                    }
                    else {
                        $("#productModal").modal("hide"); //  orModal 
                        // vm.getProducts(); // 
                        console.log("");
                    }
                });
            },

            // 
            openDeleteModal(id, title) {
                this.deleteProductID = id;        //  id 
                this.deleteProductTitle = title;  //  title 
                // console.log("deleteProductID", this.deleteProductID); // 
                // console.log("deleteProductTitle", this.deleteProductTitle); // 
                $("#productDeleteModal").modal("show");
            },

            // 
            deleteProduct() {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.deleteProductID}`;
                const vm = this;

                this.$http.delete(api).then((response) => {
                    // console.log("response", response); // 
                    if (response.data.success) {
                        vm.deleteProductID = "";    //  id
                        vm.deleteProductTitle = ""; //  title
                        vm.getProducts(); // ()
                        alert(""); // 
                        $("#productDeleteModal").modal("hide"); //  Modal 
                    }
                    else {
                        vm.deleteProductID = "";    //  id
                        vm.deleteProductTitle = ""; //  title
                        alert("");
                    }
                });
            },



        },

        created() {
            this.getProducts();
            // console.log("this.products", this.products);
        },
    };
</script>


